.search_panel{
    padding: 10px 20px;
    width: 80vw;
    min-height: 70px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    background-color: #272727;
    &__input{
        width: 95%;
        height: 100%;
        background-color: #272727;
        border: none;
        outline: none;
        color: #fff;
        font-size: 20px;
    }
    &__clear{
        margin: 0 20px 0 0;
        width: 18px;
        height: 18px;
        border: none;
        background: url('../../assets/clear.svg') center center/cover no-repeat;
    }
    &__icon{
        width: 22px;
        height: 22px;
        background: url('../../assets/search.svg') center center/cover no-repeat;
    }
}

@media (max-width: 450px) {
    .search_panel{
        padding: 5px 10px;
        width: 90vw;
        &__input{
            width: 95%;
            font-size: 18px;
        }
        &__clear{
            margin: 0 10px 0 0;
        }
        &__icon{
            width: 18px;
            height: 18px;
        }
    }
}