.player_controls{
    padding: 20px 40px;
    height: 70px;
    width: 80vw;
    display: flex;
    justify-content: start;
    align-items: center;
    background-color: #272727;
    border-radius: 10px;
    *{
        margin: 0 20px;
    }
    &__img{
        margin: 0 10px 0 0;
        width: 50px;
        height: 50px;
        object-fit: cover;
    }
    &__blank{
        width: 50px;
        height: 50px;
        background-color: #191919;
    }
    &__prev{
        width: 30px;
        height: 30px;
        border: none;
        background: url('../../assets/prev.svg') center center/cover no-repeat;
        &:hover{
            transform: scale(1.1);
        }
    }
    &__next{
        width: 30px;
        height: 30px;
        border: none;
        background: url('../../assets/next.svg') center center/cover no-repeat;
        &:hover{
            transform: scale(1.1);
        }
    }
    &__play_playBtn{
        width: 50px;
        height: 50px;
        border: none;
        border-radius: 100% !important;
        background: url('../../assets/play.svg') center center/cover no-repeat;
        background-color: #fff;
        &:hover{
            transform: scale(1.1);
        }
    }
    &__play_pauseBtn{
        width: 50px;
        height: 50px;
        border: none;
        border-radius: 100% !important;
        background: url('../../assets/pause.svg') center center/cover no-repeat;
        background-color: #fff;
        &:hover{
            transform: scale(1.1);
        }
    }
    &__descr{
        margin: 0 10px 0 0 !important;
        width: 150px;
        overflow: hidden;
    }
    &__descr_name{
        margin: 0;
        color: #fff;
        font-size: 16px;
    }
    &__descr_singer{
        margin: 0;
        color: #fff;
        opacity: 50%;
        font-size: 12px;
    }
    &__duration_range{
        color: #707070;
        background-color: #fff;
    }
}

@media (max-width: 450px) {
    .player_controls{
        padding: 5px 10px;
        min-height: 90px;
        width: 90vw;
        flex-direction: row;
        flex-wrap: wrap;
        *{
            margin: 0;
        }
        &__prev{
            margin: 0 10px 0 0 !important;
            width: 20px;
            height: 20px;
        }
        &__next{
            margin: 0 10px 0 0 !important;
            width: 20px;
            height: 20px;
        }
        &__play_playBtn{
            margin: 0 10px 0 0 !important;
            width: 30px;
            height: 30px;
        }
        &__play_pauseBtn{
            margin: 0 10px 0 0 !important;
            width: 30px;
            height: 30px;
        }
        &__descr{
            margin: 0 10px 0 10px !important;
            width: 150px;
            overflow: hidden;
        }
    }
}