.player{
    &_navbar{
        padding: 50px;
        width: 320px;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        border-radius:0 20px 20px 0 ;
        background-color: #272727;
        &__link{
            margin: 20px;
            font-size: 24px;
            text-decoration: none;
            color: #fff;
            opacity: 30% !important;
            &:hover{
                opacity: 100% !important;
                transition: all 1s;
            }
        }
    }
}

@media (max-width: 450px) {
    .player{
        &_navbar{
            display: none;
        }
    }
}