.progress_bar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &__current_time, &__duration{
        margin: 0 20px;
        color: #fff;
        text-align: center;
        width: 30px;
    }
    &__range{
        height: 7px;
        width: 500px;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-color: #fff;
        &::-webkit-slider-runnable-track {
        height: 7px;
        width: 100%;
        background-color: #707070;
        }
        &::-webkit-slider-thumb {
        background: #fff;
        border-radius: 100%;
        cursor: pointer;
        width:15px;
        height: 15px;
        -webkit-appearance: none;
        margin-top: -4px;
        }
        &::-moz-range-track {
            height: 5px;
            background-color: #fff;
        }
        &::-moz-range-thumb {
            background: #fff;
            border-radius: 100%;
            cursor: pointer;
        }
    }
}

@media (max-width: 450px) {
    .progress_bar{
        &__current_time, &__duration{
            margin: 0 10px;
            font-size: 12px;
            width: 20px;
        }
        &__range{
            width: 250px;
        }
    }
}