.music_list{
    width: 80vw;
    height: 75vh;
    display: flex;
    flex-direction: row;
    align-items: space-between;
    flex-wrap: wrap;
    overflow: scroll;
    &::-webkit-scrollbar{
        display: none;
    }
    &_item{
        position: relative;
        margin: 20px;
        width: 260px;
        height: 360px;
        padding: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        list-style-type: none;
        border-radius: 10px;
        border: 2px solid #272727;
        &__img{
            width: 100%;
            height: 300px;
            object-fit: cover;
            user-select: none;
        }
        &__name{
            margin-top: 10px;
            color: #FFF;
            font-family: Roboto;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            user-select: none;
        }
        &__singer{
            margin-top: 10px;
            width: 100%;
            color: #fff;
            opacity: 50% !important;
            font-family: Roboto;
            font-size: 12px;
            font-style: normal;
            font-weight: 300;
            line-height: normal;
            user-select: none;
        }
        &__like{
            position: absolute;
            width: 20px;
            height: 20px;
            right: 20px;
            bottom: 20px;
            //background: url('../../assets/like.svg') center center no-repeat;
            &:hover{
                transform: scale(1.1);
                fill: red;
            }
        }
    }
}

.active{
    border: 2px solid #999999;
}

@media (max-width: 450px) {
    .music_list{
        &_item{
            margin: 10px;
            width: 130px;
            height: 195px;
            padding: 10px;
            &__img{
                height: 200px;
            }
            &__name{
                font-size: 12px;
            }
            &__singer{
                font-size: 8px;
            }
            &__like{
                position: absolute;
                width: 10px;
                height: 10px;
                right: 10px;
                bottom: 10px;
                //background: url('../../assets/like.svg') center center no-repeat;
                &:hover{
                    transform: scale(1.1);
                    fill: red;
                }
            }
        }
    }
}