.volume{
    margin: 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    &_icon{
        margin: 0 20px;
        width: 30px;
        height: 24px;
        border: none;
        background: url('../../assets/volume.svg') center center/cover no-repeat;
    }
    &_range{
        height: 7px;
        width: 80px;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-color: #fff;
        &::-webkit-slider-runnable-track {
        height: 7px;
        width: 100%;
        background-color: #707070;
        }
        &::-webkit-slider-thumb {
        background: #fff;
        border-radius: 100%;
        cursor: pointer;
        width:15px;
        height: 15px;
        -webkit-appearance: none;
        margin-top: -4px;
        }
        &::-moz-range-track {
            height: 5px;
            background-color: #fff;
        }
        &::-moz-range-thumb {
            background: #fff;
            border-radius: 100%;
            cursor: pointer;
        }
    }
}

@media (max-width: 450px) {
    .volume{
        display: none !important;
    }
    
}